import React from "react";
import '@wcj/dark-mode';
import { P } from "./StyleComponent"
import Button from "./StyleComponent/Button"
import Input from "./StyleComponent/Input"
import Label from "./StyleComponent/Label"

import Theme from "./themes"
import themeJson from "./theme.json"
function App() {
  const [count, setCount] = React.useState(1)

  return (
    <div className="body" >
      <dark-mode permanent></dark-mode>
      <div className="one" >
        {count}
      </div>
      <Theme cssVariable={themeJson} type="var" >
        <div className="dot" >
          {count}
        </div>
      </Theme>
      <Input type={"color"} />
      <br />
      <Label>
        <Input type={"checkbox"} />
      </Label>
      <br />
      <Button onClick={() => setCount(count + 1)} >styled-components </Button>
      <P>styled-components p</P>
      <Button style={{ color: "red" }} >button</Button>
      <Button size="large"  >button</Button>
      <Button types="primary" >button</Button>
      <Button types="light" focus >light</Button>
      <Button disabled >button</Button>
      <Button types="danger" >button</Button>
      <Button types="success" >button</Button>
      <Button block >button</Button>
      <Button active >button</Button>
      <Button basic >button</Button>
      <Button focus >button</Button>
    </div>
  );
}

export default App;
